import { Button, Modal } from "react-bootstrap";
import { WalletsShow } from "../../../api/login/Login";
import { useEffect, useState } from "react";

function ModalVideoKycSet(props) {
  const [user, setUser] = useState();
  useEffect(() => {
    (async () => {
      const res = await WalletsShow();
      setUser(res?.data?.data);
    })();
  }, []);

  return (
    <>
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        backdrop="static"
      >
        <Modal.Header>
          <Modal.Title
            id="contained-modal-title-vcenter"
            style={{ color: "2e3191" }}
          >
            Video KYC Instructions /   
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p style={{ lineHeight: "37px", fontWeight: "700" }}>
            My name is <u>{user?.pan_name}</u>. My shop name is <u>{user?.business_name}</u> I want to join BankMudraa and I
            am sharing my KYC documents to complete the process.   <u>{user?.pan_name}</u>.
                 <u>{user?.business_name}</u>   BankMudraa   
            /    KYC      
              / 
          </p>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={props.onHide}>Agree</Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
export default ModalVideoKycSet;
